.wp-block-epicslider-blocks-three-img-center{width:100%}.epic-slider_center{position:relative;padding:0 0px;margin:5rem auto;max-width:1200px;width:80%}.epic-slider_center .slick-list div .caption{max-width:80%;width:100%;transform:translate(0%, 0%);display:flex;justify-content:center}.epic-slider_center .slick-nav{bottom:calc(25% - 44px)}.epic-slider_center .slick-list .slick-track .wp-block-epicslider-portrait{position:relative;opacity:.8;transform:scale(0.9);max-height:1100px;width:100%}.epic-slider_center .slick-list .slick-track .slider{width:80%;margin:100px auto}.epic-slider_center .slick-list .slick-track .slick-slide{margin:10px 20px;transition:all 500ms ease-in-out 100ms}.epic-slider_center .slick-list .slick-track .slick-slide img{width:100%;max-height:1100px}.epic-slider_center .slick-list .slick-track .slick-center{transform:scale(1.1)}.epic-slider_center .slick-list .slick-track .slick-center .wp-block-epicslider-portrait{position:relative;opacity:1}@media only screen and (max-width: 1000px){.epic-slider_center{width:100%;margin:5rem 0}.epic-slider_center .slick-list .slick-track .slick-slide{margin:10px 10px;transition:all 500ms ease-in-out 100ms}}
